          div.gridsheet div.bottombar div.sheetbuttonscrollcontainer div.sheetbuttoncontainer div.sheetbutton input, div.gridsheet div.bottombar div.sheetbuttonscrollcontainer div.sheetbuttoncontainer div.active input {
            position: relative;
